body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  display: flex;
  justify-content: space-evenly;
  background-color: #512DA8;
  height: 60px;
  padding: 0 50px;
}

header h1 {
  font-size: 1.5rem;
  font-family: "P22 Frau Jenson People";
  color: #FFFFFF;
  margin: auto;
}

header h2 {
  font-size: 1.5rem;
  font-family: "Georama", sans-serif;
  color: #FFFFFF;
  margin: auto;
}

header .home {
  color: #FFFFFF;
}
/*# sourceMappingURL=Header.css.map */
.listItem {
  background-color: #D1C4E9;
  display: flex;
  flex-direction: column;
  border-radius: 20px 0 20px 0;
  box-shadow: 1px 5px 10px #D1C4E9;
  height: 250px;
  width: 160px;
  transition: 500ms;
}

.listItem:hover {
  transform: translateY(-5px);
}

.listItem img {
  border-radius: 10px 0 10px 0;
  width: 100%;
  height: auto;
}

.listItem .basicInfo {
  font-family: "Georama", sans-serif;
  color: #512DA8;
  padding: 3px;
}

.listItem .basicInfo h6 {
  text-align: center;
}

.listItem .basicInfo div {
  display: flex;
  width: 100%;
  justify-content: space-around;
}
/*# sourceMappingURL=ListItem.css.map */
.userContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 30px 0;
  width: 100%;
  min-height: 500px;
  grid-gap: 10px;
  gap: 10px;
  overflow: hidden;
  padding: 10px;
}

.link {
  text-decoration: none;
}

.image {
  width: 70%;
  margin: auto;
}

.imgContainer {
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
}

form {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 100px auto;
}

form div {
  display: flex;
  height: 40px;
  align-items: center;
  justify-content: space-around;
}

form div label p {
  font-family: "Montserrat", sans-serif;
  margin-bottom: 0;
}

form div input {
  width: 50px;
}

form button {
  border: none;
  background-color: #D1C4E9;
  color: #E040FB;
  font-weight: 600;
  font-size: 1.2rem;
}
/*# sourceMappingURL=Home.css.map */
#user {
  min-height: 90.5vh;
  padding-top: 50px;
  overflow: hidden;
}

#mapid {
  height: 300px;
  width: 100%;
  position: relative;
  bottom: 0;
  margin-top: 50px;
}

.basicInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 220px;
  color: #212121;
}

.basicInfo h3 {
  font-size: 1.0rem;
  font-family: "Montserrat", sans-serif;
}

.basicInfo p {
  font-family: "Montserrat", sans-serif;
}

.basicInfo img {
  border-radius: 50%;
}

.mainInfo {
  font-family: "Georama", sans-serif;
  color: #757575;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
}

.mainInfo a {
  text-decoration: none;
  color: #757575;
}

.mainInfo .info {
  display: flex;
  align-items: center;
  margin: auto 0;
}

.mainInfo .info p, .mainInfo .info a {
  margin: 0 10px;
}
/*# sourceMappingURL=User.css.map */
